body {
    line-height: 24px;
    font-size: 14px;
    font-family: "Poppins", sans-serif;
    font-weight: normal;
    color: #7f7f7f;
}
a {
    text-decoration: none;
    transition: all 0.3s ease-in-out;
}
h1, h2, h3, h4, h5, h6 {
    font-family: "Poppins", sans-serif;
    font-weight: bold;
}